@use "global" as *;

.voice-card {
  box-shadow: rem(2) rem(2) rem(10) rgba($color: $black2, $alpha: 0.25);
  padding: rem(15) rem(16) rem(25) rem(16);
  @include mq("md") {
    padding: rem(16) rem(22) rem(20);
  }
}

.voice-card__title {
  margin-top: rem(6);
  font-weight: $medium;
  line-height: 1.5;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;

  @include mq("md") {
    margin-top: rem(11);
    font-size: rem(24);
  }
}

.voice-card__title-wrap {
  margin-right: rem(13);
  border-bottom: rem(1) solid $green1;
  width: calc(51.758% - rem(13));
  // width: 47.6038%;
  // width: 58.474%;
  // padding-top: rem(24);

  @include mq("md") {
    margin-right: rem(13);
    width: calc(61.864% - rem(16));
  }
}
.voice-card__img-wrap {
  padding-top: rem(7);
  width: calc(48.242%);
  max-width: rem(151);

  @include mq("md") {
    padding-top: 0;
    margin-left: rem(16);
    width: 38.136%;
    max-width: rem(180);
  }
}
.voice-card__img-wrap img {
  aspect-ratio: 151 / 117;
  object-fit: cover;
  width: 100%;
  height: 100%;

  @include mq("md") {
    aspect-ratio: 180 / 140;
  }
}
.voice-card__meta {
  @include mq("md") {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
}
.voice-card__age {
  font-family: "Noto Sans JP", sans-serif;
  font-size: rem(12);
  line-height: 1;
  color: $black1;
}
.voice-card__label {
  margin-top: rem(2);
  margin-left: auto;
  display: inline-block;
  border: rem(1) solid $green1;
  padding: rem(8) rem(12) rem(8) rem(10);
  font-size: rem(12);
  line-height: 1;
  color: $green1;

  @include mq("md") {
    padding: rem(10) rem(10);
    margin-top: rem(0);
  }
}
.voice-card__text {
  margin-top: rem(15);
  line-height: 1.5;

  @include mq("md") {
    margin-top: rem(43);
  }
}
