@use "global" as *;

.top-about__inner {
  position: relative;
}

.top-about__inner::before {
  @include mq("md") {
    content: "";
    position: absolute;
    width: rem(194);
    height: rem(181);
    bottom: rem(-90);
    right: calc(50% - min(48.5vw, rem(640)));
    background-image: url(../images/common/top-about-coral.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
}

.top-about__inner.top-about__inner--change-illustration::before {
  @include mq("md") {
    width: rem(301);
    height: rem(138);
    background-image: url(../images/common/fish3.png);
    bottom: rem(-178);
  }
}

.top-about__img-wrap {
  margin-top: rem(24);

  @include mq("md") {
    margin-top: rem(40);
  }
}

.top-about__img-wrap.top-about__img-wrap--noheading {
  margin-top: 0;
}

.top-about__img-small {
  width: 37.1014%;
  position: relative;
  z-index: 2;
}

.top-about__img-small img {
  aspect-ratio: 128 / 194;
  object-fit: cover;
  width: 100%;
  height: 100%;

  @include mq("md") {
    aspect-ratio: 400 / 606;
  }
}

.top-about__img-big {
  width: 81.45%;
  margin-top: -49%;
  margin-left: auto;
  z-index: 1;

  @include mq("md") {
    margin-top: -48.5%;
  }
}

.top-about__img-big.top-about__img-big--bigger {
  margin-top: 0;
  width: 100%;

  @include mq("md") {
    width: 81.45%;
    margin-top: -48.5%;
    margin-left: auto;
    z-index: 1;
  }
}

.top-about__img-big img {
  aspect-ratio: 281 / 186;
  object-fit: cover;
  width: 100%;
  height: 100%;

  @include mq("md") {
    aspect-ratio: 880 / 581;
  }
}

.top-about__content-wrap {
  margin-top: rem(40);

  @include mq("md") {
    margin-top: 0;
    width: 74.78%;
    position: absolute;
    top: 40%;
    left: 19.8%;
    z-index: 2;
    display: flex;
  }
}

.top-about__content-wrap.top-about__content-wrap--erase {
  display: contents;

  @include mq("md") {
    display: flex;
  }
}

.top-about__content-title {
  display: block;
  font-size: rem(40);
  font-weight: $regular;
  line-height: 1;
  color: $green1;
  // text-align: center;

  @include mq("md") {
    width: 50%;
    font-size: rem(80);
    line-height: 1.25;
    color: $white;
  }
}

.top-about__title.top-about__title--on-img {
  position: absolute;
  top: rem(24);
  left: rem(39);
  color: $white;

  @include mq("md") {
    position: static;
  }
}

.top-about__content-text-wrap {
  margin-top: rem(19);

  @include mq("md") {
    margin-top: 0;
    margin-left: rem(38);
    width: 50%;
  }
}

.top-about__content-text-wrap.top-about__content-text-wrap--about-margin {
  margin-top: rem(40);

  @include mq("md") {
    margin-top: 0;
  }
}

.top-about__content-text {
  line-height: 1.5;
  @include mq("md") {
    color: $white;
  }
}

.top-about__button {
  margin-top: rem(45);
  text-align: center;
  @include mq("md") {
  }
}

.top-about__illustration {
  @include mq("md") {
    position: absolute;
    width: rem(194);
    right: -0.981%;
    bottom: rem(-90);
  }
}
