@use "global" as *;

.campaign-card {
  box-shadow: rem(2) rem(2) rem(10) rgba($color: $black2, $alpha: 0.25);
  display: flex;
  flex-direction: column;
}
.campaign-card__img-wrap img {
  aspect-ratio: 333 / 223;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.campaign-card__body {
  padding: rem(16) rem(16) rem(17);
  flex-grow: 1;
  display: flex;
  flex-direction: column;

  @include mq("md") {
    padding-top: rem(24);
    padding-bottom: rem(29);
  }
}
.campaign-card__category {
  display: flex;
  flex-wrap: wrap;
  column-gap: rem(10);
  row-gap: rem(10);
}
.campaign-card__label {
  display: inline-block;
  border: rem(1) solid $green1;
  padding: rem(8) rem(11);
  font-size: rem(12);
  line-height: 1;
  color: $green1;

  @include mq("md") {
    padding: rem(10) rem(16);
  }
}
.campaign-card__title {
  margin-top: rem(8);
  font-size: rem(16);
  font-weight: $medium;
  line-height: 1.5;
  padding-bottom: rem(3);
  border-bottom: rem(1) solid $green1;

  @include mq("md") {
    margin-top: rem(10);
    padding-bottom: rem(2);
  }
}
.campaign-card__wrap {
  margin-top: rem(15);
  padding: 0 rem(19) 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include mq("md") {
    margin-top: rem(23);
  }
}
.campaign-card__text {
  text-align: center;
  line-height: 1;
  font-size: rem(14);
  flex-grow: 1;

  @include mq("md") {
    font-size: rem(16);
  }
}
.campaign-card__price {
  margin-top: rem(3);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: $second-font-family;
  font-weight: $bold;

  @include mq(md) {
    margin-top: rem(2);
  }
}
.campaign-card__price-before {
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1;
  display: flex;
  align-items: center;
  padding-top: rem(3);
  position: relative;

  @include mq("md") {
    font-size: rem(24);
  }
}
.campaign-card__price-before::before {
  content: "";
  position: absolute;
  display: block;
  background-color: $red;
  width: 100%;
  height: rem(3);
  top: 53%;
  left: 0;
}
.campaign-card__price-after {
  margin-left: rem(20);
  font-size: rem(32);
  color: $green1;

  @include mq("md") {
    margin-left: clamp(0.1rem, 1.736vw, 1.5625rem);
    font-size: rem(40);
  }
}
