@use "global" as *;

.loader {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
}
.loader__line {
  width: calc((100% / 2) + 1px);
  margin-left: -1px;
  height: 100%;
  background: $white;
}
.loader__text {
  // text-align: center;
  // align-items: center;
  color: $green1;
}
